import { TableRowColumn } from './table.api_row_column.abstract';

/**
 * 列对象
 */
export class TableColumn extends TableRowColumn {
    /**
     * 获取列对象的统一横坐标
     * @returns 坐标
     */
    public getUnifiedPosition () { // 统一纵坐标
        return {
            x: this.unifiedPosition,
        };
    }

    /**
     * 设置列宽
     * @param width 列宽
     */
    public setWidth (width: number) {
        this.size = width;

        this.externalContext.emitCellEvent('CELL_SET_SIZE', {
            x: undefined,
            y: this.index - 1,
            height: undefined,
            width,
        });
    }
    
    /**
     * 获取列宽
     * @returns 列宽
     */
    public getWidth () {
        return this.size;
    }

    /**
     * 获取第row行的单元格
     * @param row 行
     * @returns 单元格
     */
    public getCell(row: number) {
        return this.externalContext.getCell(this.index, row);
    }
}
